/* ------------------------------------------------------------------------------------------------ */
/* General */
/* ------------------------------------------------------------------------------------------------ */

body {
  background-image: url(../images/body-bg-repeat.jpg);
  background-size: 100% auto;
  background-position: center;
  position: relative;
  &:before {
    content: "";
    width: 100%;
    height: 100vh;
    background-image: url(../images/body-bg-top.jpg);
    background-size: 100% auto;
    background-position: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0; } }
#wrap {
  position: relative;
  z-index: 1;
  padding-top: 195px; }


.btn {
  font-size: 16px;
  line-height: 26px;
  font-weight: 500;
  border: 0;
  display: inline-block;
  padding: 6px 20px;
  border-radius: 0;
  &-primary {
    background-color: $primary;
    color: #FFF;
    &:hover {
      background-color: darken( $primary, 10%); } }
  &-white {
    background-color: #FFF;
    color: #333;
    &:hover {
      background-color: darken( #FFF, 10%); } }
  &-block {
    width: 100%; } }

