/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */
.header {
  padding-top: 50px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;

  &_top {
    margin-bottom: 1rem;
    .row {
      align-items: center; } }
  &_logo {
    display: inline-block;
    transition: all .3s ease-in-out;
    img {
      max-height: 80px;
      transition: all .3s ease-in-out; } }
  &_search {
    form {
      display: flex;
      justify-content: center;
      width: 100%; }
    .form-group {
      width: 100%;
      max-width: 400px;
      border-radius: 5px;
      .form-control {
        width: calc(100% - 50px);
        display: inline-block;
        vertical-align: middle;
        border-radius: 5px 0 0 5px;
        line-height: {}
        max-height: 33px; }
      button {
        display: inline-block;
        vertical-align: middle;
        width: 50px;
        height: 33px;
        border: 0;
        border-radius: 0 5px 5px 0;
        background: $primary;
        margin-left: -5px; } } }
  &_right {
    display: flex;
    justify-content: flex-end;
    margin-left: -12px;
    margin-right: -12px;
    &-icon,
    &-currency {
      padding: 0 12px; }
    &-icon {
      img {
        width: 30px;
        height: 30px;
        object-fit: contain;
        object-position: center; } }
    &-currency {
      .select {
        max-width: 50px;
        font-size: 12px;
        line-height: 1.5em; }
      .bootstrap-select.select > .dropdown-toggle {
        padding: 5px 6px;
        font-size: 12px;
        line-height: 1.5em;
        background: transparent;
        border-radius: 0;
        color: #FFF;
        &:hover,
        &:focus,
        &:active,
        &:visited {
          box-shadow: none !important;
          outline: none !important; } } } }
  &_menu {}

  &.sticky {
    padding-top: 10px;
    background: #000000;
    .header {
      &_top {
        margin-top: -58px; }
      &_logo {
        position: absolute;
        top: 32px;
        img {
          max-height: 30px; } } } }

  .menu-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: -30px;
    margin-right: -30px;
    & > li {
      padding-left: 30px;
      padding-right: 30px;
      padding-bottom: 14px;
      a {
        color: #FFF; }
      & > a,
      & > span {
        font-weight: 700; } }
    li.has-sub {
      position: relative;
      &:hover {
        .submenu {
          top: 100%;
          opacity: 1;
          visibility: visible;
          max-height: max-content; } } }

    .submenu {
      position: absolute;
      top: 50%;
      left: 30px;
      background: #271910CC;
      padding: 6px 10px;
      max-height: 0;
      opacity: 0;
      visibility: hidden;
      transition: all .3s ease-in-out;
      li:not(:last-child) {
        margin-bottom: 12px; } } } }
