/* ------------------------------------------------------------------------------------------------ */
/* Form */
/* ------------------------------------------------------------------------------------------------ */

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

input,
button,
.btn {
  &:hover,
  &:focus,
  &:active,
  &:visited {
    box-shadow: none !important;
    outline: none !important; } }

.form-group {
  label {
    display: block; }
  .select {
    width: 100% !important; } }

.form-control {
  background: #0000004D;
  border-radius: 5px;
  border: 1px solid $border;
  &::placeholder {
    color: #FFF; } }

.custom-control {
  &:not(:last-child) {
    margin-block-end: 1rem; }
  &.custom-checkbox {
    .custom-control {
      &-label {
        &:before,
        &:after {
          background: transparent;
          border: 0;
          border-radius: 0; }
        &::before {
          border: 1px solid $border; } }
      &-input:checked {
        & ~ .custom-control-label::before {
          background: $primary; }
        & ~ .custom-control-label::after {
          background-repeat: no-repeat;
          background-size: 60%;
          background-position: center; } } } } }

textarea.form-control {
  min-height: 130px; }

.group-chkbox {
  margin-bottom: 1rem;
  .custom-control {
    margin-bottom: 10px; } }

.form-action {
  .btn {
    min-width: 180px;
    & + .btn {
      margin-left: 24px; } } }

// Select
.bootstrap-select.select > .dropdown-toggle {
  padding: 5px 10px;
  font-size: 14px;
  line-height: 1.6em;
  background: #0000004D;
  border: 1px solid $border;
  border-radius: 5px;
  color: #FFF;
  width: 100%;
  &:hover,
  &:focus,
  &:active,
  &:visited {
    box-shadow: none !important;
    outline: none !important; } }

.upImg-group {
  .row {
    margin-left: -8px;
    margin-right: -8px; }
  .col {
    flex: 0 0 calc(100% / 5);
    max-width: 155px;
    padding-left: 8px;
    padding-right: 8px; } }

// Upload File
.upload-file {
  position: relative;
  max-width: 155px;
  margin: 0 auto;
  .inputfile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1; }
  .add-photo {
    width: 100%;
    padding-top: 117%;
    border: 1px solid $border;
    border-radius: 5px;
    display: block;
    position: relative;
    background-repeat: no-repeat;
    background-position: center;
    background: #0000004D;
    // opacity: .4
    transition: all 0.3s;
    .image-preview {
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      border-radius: 5px;
      top: 0;
      left: 0;
      background-color: transparent;
      background-image: url(../images/placeholder-upload-photo.png);
      @include bgContain;
      background-size: 100px 70px;
      transition: all 0.3s; } }
  .btn-del {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 15px;
    background-color: $primary;
    color: #FFF;
    opacity: 0;
    font-weight: normal;
    transition: all 0.3s;
    z-index: 2; }
  &.has-file {
    .image-preview {
      @include bgCover; }
    &:hover {
      .btn-del {
        opacity: .9;
        visibility: visible; } } } }
