/* ------------------------------------------------------------------------------------------------ */
/* Footer */
/* ------------------------------------------------------------------------------------------------ */
.footer {
  &_top {
    background: radial-gradient(125.48% 125.48% at 50% 59.87%, rgba(0, 0, 0, 0) 0%, #000000 90.33%); }
  &_wrap {
    padding: 20px 0;
    position: relative;
    .left,
    .right {
      display: flex;
      justify-content: space-between; } }
  &_menu {
    min-width: 150px;
    ul {
      li:not(:last-child) {
        margin-bottom: 8px; } } }
  &_contact {
    max-width: 215px;
    h4 {
      font-size: 14px;
      line-height: 1.5em;
      margin-bottom: 4px; }
    address,
    .email {
      font-size: 12px;
      line-height: 1.5em;
      margin-bottom: 4px; } }
  &_subs {
    flex: 0 0 50%;
    max-width: 280px;
    p {
      font-size: 12px;
      line-height: 1.5em;
      margin-bottom: 10px; }
    .form-group {
      position: relative;
      &::after {
        content: "";
        position: absolute;
        top: 6px;
        right: 12px;
        width: 20px;
        height: 20px;
        background: url(../images/ic-mail.svg);
        @include bgContain; } }
    .form-control {
      background: #000000;
      border: 1px solid #FFF;
      border-radius: 5px;
      color: #FFF; } }
  &_logo {
    flex: 0 0 50%;
    max-width: 300px;
    img {
      width: auto;
      max-height: 130px;
      object-fit: contain;
      object-position: center; } }
  &_bottom {
    text-align: center;
    font-size: 12px;
    line-height: 1.5em;
    padding: 10px 0;
    background: #000000;
    p {
      margin-bottom: 0; } } }
