/* ------------------------------------------------------------------------------------------------ */
/* Content */
/* ------------------------------------------------------------------------------------------------ */

.cart {
  padding-bottom: 80px;
  &_header {
    padding: 10px 0;
    border-top: 1px solid $border;
    border-bottom: 1px solid $border;
    h4 {
      font-size: 1rem;
      line-height: 1.5em;
      margin-bottom: 0;
      font-weight: 400; }
    .col-md-2 {
      text-align: center; } }
  &_wrap {
 }    // padding: 20px 0
  &_item {
    border-bottom: 1px solid $border;
    padding: 20px 0;
    .row {
      align-items: center; }
    .col-md-2 {
      text-align: center; } }
  &_action {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .delete {
      width: 20px;
      height: 20px;
      display: block;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center; } } }
  &_product {
    display: flex;
    align-items: center;
    &-img {
      flex: 0 0 120px;
      max-width: 120px;
      margin-right: 24px;
      border: 2px solid $border;
      padding: 9.923% 0;
      position: relative;
      img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        object-fit: cover;
        object-position: center; } }
    &-name {
      flex: 0 0 100%;
      max-width: calc(100% - 145px);
      h4 {
        font-size: 18px;
        max-width: 430px; } } }

  &_total {
    .agreement {
      padding-top: 20px;
      a {
        text-decoration: underline; } }
    .grand_total {
      display: flex;
      align-items: flex-end;
      width: 100%;
      max-width: 580px;
      border-bottom: 1px solid $border;
      margin-bottom: 20px;
      .row {
        width: 100%;
        margin-left: -2px;
        margin-right: -2px;
        align-items: center;
        .col-md-4 {
          padding-left: 2px;
          padding-right: 2px; } }
      h4 {
        margin-bottom: 0;
        font-size: 16px; }
      .total_qty,
      .total_nominal {
        background: #fff;
        padding: 8px;
        color: #000;
        text-align: center; } }

    .total_action {
      width: 100%;
      display: flex;
      justify-content: flex-end;
      .btn-primary {
        max-width: 380px; } } } }



// Checkout & Form Page
.checkout,
.registeration,
.form-page {
  padding-bottom: 80px;
  .text-description {
    max-width: 1030px;
    margin-bottom: 30px;
    h2 {
      font-size: 2.25rem;
      line-height: 1.1em;
      margin-bottom: 16px; } }
  form {
    &:not(:last-of-type) {
      margin-bottom: 50px; } }
  .terms {
    margin-bottom: 1rem;
    font-size: 12px;
    line-height: 1.5em;
    a {
      text-decoration: underline; } } }

// About Us
.aboutus {
  padding-bottom: 80px;
  h2 {
    text-transform: uppercase;
    font-size: 2.25rem;
    line-height: 1.1em;
    margin-bottom: 16px; }
  &_description {
    margin-bottom: 30px; }
  &_friends {
    &-item {
      &:not(:last-child) {
        margin-bottom: 30px; }
      h2 {
        font-weight: normal; }
      p {
        font-size: 14px;
        line-height: 1.5em; } }
    &-detail {
      padding-top: 20px;
      border-top: 1px solid $border; } } }

// Accountpage
.accountpage {
  .account_form {
    margin-bottom: 50px;
    .upload-file {
      margin-bottom: 20px;
      .add-photo {
        padding: 50% 0; } } }
  .shipping_form {
    margin-bottom: 50px; }
  .addresslist {
    margin-bottom: 50px;
    &_item {
      padding: 10px 0;
      border-top: 1px solid $border;
      border-bottom: 1px solid $border;
      display: flex;
      justify-content: space-between;
      align-items: center;
      & + .addresslist_item {
        border-top: 0; } }
    &_address {
      flex: 0 0 80%;
      max-width: 80%;
      h5 {
        font-size: 15px;
        line-height: 1.5em;
        margin-bottom: 0; } }
    &_edit {
      flex: 0 0 20%;
      max-width: 20%;
      text-align: right; } } }


// Private Vault
.privatevault {
  padding-bottom: 80px;
  &_description {
    margin-bottom: 50px;
    &-wrap {
      padding: 24px 30px;
      background: #E5E5E5;
      color: #333;
      & > .row {
        justify-content: space-between; }
      h2 {
        font-size: 36px;
        line-height: 1.5em;
        color: #333;
        margin-bottom: 30px;
        padding-bottom: 28px;
        border-bottom: 2px solid #333; }
      article {
        font-size: 14px;
        line-height: 1.5em;
        text-align: justify;
        h3 {
          font-size: 24px;
          line-height: 1.1em;
          color: #333;
          text-transform: uppercase;
          margin-bottom: 16px; }
        h4 {
          font-size: 18px;
          line-height: 1.5em;
          margin-bottom: 16px;
          color: #333; }
        ul {
          list-style: square; } } } }
  &_guitar {
    &-card {
      margin-bottom: 40px;
      .imgbox {
        width: 100%;
        padding: 68.63% 0;
        position: relative;
        img {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          object-fit: cover;
          object-position: center; } }
      .textbox {
        border-top: 2px solid $primary;
        background: #E5E5E5;
        padding: 13px;
        h3 {
          color: #333;
          font-size: 14px;
          line-height: 1.5em;
          margin-bottom: 0;
          a {
            color: #333; } } } } } }
